.h-content{
    border-top-left-radius: 5em;
    border-top-right-radius: 5em;

    /* background-color: #0B2341; */
    /* background-color: #191818; */

    background-color: #0A100D;

    /* margin-top: 40vh; */
    padding-top: 100px;
    padding-bottom: 50px;
    min-height: auto;
}

.h-title-about{
    padding-left: 100px;
    font-weight: 300;
}

.h-title-about:nth-child(1){
    font-size: 2em;
    width: 53%;
    /* line-height: 1.3; */
}

.h-title-about:nth-child(2){
    font-size: 1.2em;
    width: 40%;
    color: rgba(255, 255, 255, 0.900);
}

.h-title-about:nth-child(2) a{
    background-color: #C5D86D;
}

.h-title-porto{
    text-align: end;
    padding-right: 20%;
    font-size: 3.5em;
}

.h-title-porto i{
    transform: rotate(40deg) !important;
}

.h-some-porto-top{
    margin-top: 300px;
}

.h-some-porto-top p{
    padding-bottom: 50px;
}

.h-some-porto-top-left-image{
    width: 400px;
    aspect-ratio: 9/12;
    margin-right: 50px;
    margin-bottom: 50px;
}

.h-some-porto-top-right-image{
    height: 380px;
    aspect-ratio: 16/10;
}

.h-some-porto-bottom p{
    margin-top: 30px;
    margin-right: 50px;
    font-size: 2em;
}

.h-some-porto-bottom div{
    width: 30%;
}

.h-some-porto-bottom .btn{
    background-color: #B9BAA3;
}

.h-some-porto-bottom img{
    width: 450px;
    aspect-ratio: 1/1;
    margin-right: 50px;
    margin-bottom: 50px;
}

@media only screen and (min-width: 768px) and (max-width: 1024px){
    .h-content{
        border-top-left-radius: 5em;
        border-top-right-radius: 5em;
        padding-top: 40px;
    }

    .h-title-about{
        padding: 0px 5%;
    }
    
    .h-title-about:nth-child(1){
        width: 100%;
    }
    
    .h-title-about:nth-child(2){
        width: 100%;
        color: rgba(255, 255, 255, 0.900);
    }
}

@media only screen and (max-width: 767px){
    .h-content{
        border-top-left-radius: 50px;
        border-top-right-radius: 50px;
        padding-top: 40px;
    }
    
    .h-title-about{
        padding: 0px 5%;
    }
    
    .h-title-about:nth-child(1){
        font-size: 24px;
        width: 100%;
        /* line-height: 1.3; */
    }
    
    .h-title-about:nth-child(2){
        font-size: 15px;
        width: 100%;
        color: rgba(255, 255, 255, 0.900);
    }
}